import React, { FC, forwardRef, memo, useCallback } from 'react';
import cn from 'classnames';

import { RadioButton } from '../RadioButton';

import { RadioGroupProps, RadioGroupOption } from './RadioGroup.types';

export const RadioGroup: FC<RadioGroupProps> = memo(
  forwardRef<HTMLDivElement, RadioGroupProps>(
    (
      { className, label, disabled, options, direction = 'column', onChange, currentlySelectedOption }: RadioGroupProps,
      ref
    ) => {
      const radioCnString = cn(['thuiRadioGroup', className]);

      const onSelect = (selectedOption: RadioGroupOption) => () => {
        onChange?.(selectedOption);
      };

      const isSelected = useCallback(
        (option: RadioGroupOption) => {
          return currentlySelectedOption ? currentlySelectedOption.value === option.value : option?.defaultSelected;
        },
        [currentlySelectedOption]
      );

      return (
        <div className={radioCnString} ref={ref}>
          <span className="thuiRadioGroup__label">{label}</span>
          <div className={cn(['thuiRadioGroup__wrapper', direction])}>
            {options.map((option) => (
              <div className="thuiRadioGroup__item" key={`option-${option.value}`}>
                <RadioButton
                  disabled={disabled || option.disabled}
                  checked={isSelected(option)}
                  defaultChecked={option.defaultSelected}
                  onChange={onSelect(option)}
                >
                  {option.label}
                </RadioButton>
              </div>
            ))}
          </div>
        </div>
      );
    }
  )
);
